import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { ReceiptsService } from "../../shared/services/receipts.service";

@Component({
  selector: 'ff-receipt',
  templateUrl: './receipt.component.html'
})

export class ReceiptComponent implements OnInit {
  receipts;
  id;

  constructor(private route: ActivatedRoute, private data: ReceiptsService) {}

  ngOnInit() {

    this.route.params.subscribe((response: Response) => {
      this.id = response['id'];
    });

    this.receipts = this.data.getReceipts();
  }
}


