import { Component, OnInit } from "@angular/core";
import { HotPaymentsService } from "../../../shared/services/hot-payments.service";
import { ActivatedRoute } from "@angular/router";

@Component({
  selector: 'ff-hot-payments',
  templateUrl: './hot-payments.component.html'
})

export class HotPaymentsComponent implements OnInit {
  id: string;
  payments: any;

  constructor(private route: ActivatedRoute, private hotPayments: HotPaymentsService) {}

  ngOnInit() {

    this.route.params.subscribe((response: Response) => {
      this.id = response['id'];
    });
    this.payments = this.hotPayments.getHotpayments();
  }
  
}


