import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { AppRoutesModule } from './app-routes.module';
import { CompanyModule } from './company/company.module';
import { CompanyService } from './shared/services/company.service';
import { HotPaymentsComponent } from './company/bills/hot-payments/hot-payments.component';
import { HotPaymentsService } from './shared/services/hot-payments.service';
import { PaymentsModule } from './payments/payments.module';
import { PaymentsService } from './shared/services/payments.service';
import { ReceiptsModule } from './receipts/receipts.module';
import { ReceiptsService } from './shared/services/receipts.service';

@NgModule({
  declarations: [
    AppComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    AppRoutesModule,
    CompanyModule,
    PaymentsModule,
    ReceiptsModule
  ],
  providers: [
    CompanyService,
    HotPaymentsService,
    PaymentsService,
    ReceiptsService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



