import { Injectable } from "@angular/core";
import { DATA } from "../repository/data.repository";

@Injectable()
export class HotPaymentsService {
  private data = DATA.payments;

  getHotpayments() {
    return this.data.filter(payment => {
      return payment.date.getDate() == new Date().getDate() &&
             payment.date.getFullYear() == new Date().getFullYear() &&
             payment.date.getMonth() == new Date().getMonth();
    });
  }
}


